import { Card, Alert } from 'react-bootstrap';
import classNames from 'classnames';
// components

//const colors = ['primary', 'secondary', 'success', 'danger', 'warning', 'info', 'light', 'dark'];
const colors = ['primary'];
const MensajeBienvenida = (props): React$Element<React$FragmentType> => {
    // const icons = ['dripicons-checkmark', 'dripicons-wrong', 'dripicons-warning', 'dripicons-information'];
    const icons = ['dripicons-checkmark'];
    let text = props.mensage.replace('Pages/Aerolineas', 'Aerolineas');
    const mens = text === 'Aerolineas' ? '!' : ' sabemos que quieres viajar por ' + text;
    return (
        <Card>
            <Card.Body>
                {colors.map((color, index) => {
                    return (
                        <Alert variant={color} key={index}>
                            <i className={classNames(icons[index], 'me-2')}></i>
                            <strong>Hola, bienvenido</strong>
                            {mens}
                        </Alert>
                    );
                })}
            </Card.Body>
        </Card>
    );
};
export default MensajeBienvenida;
