// @flow
import React from 'react';
import { Row, Col, Card } from 'react-bootstrap';

import cardImg4 from '../../assets/images/logo-avianca.jpg';
// components
import PageTitle from '../../components/PageTitle';
import MensajeBienvenida from './MensajeBienvenida';

const CardWithImage2 = () => {
    return (
        <Card className="d-block">
            <Card.Img src={cardImg4} />
            <Card.Body>
                <Card.Title as="h5"> Avianca Airlines Colombia</Card.Title>
                <Card.Text>
                    Avianca te lleva por todo el mundo de la manera más segura y sin complicaciones. Tu bienestar es
                    nuestra prioridad, y queremos ayudarte a descubrir tu siguiente destino. Viaja por Toda América.
                    Vuela sin Escalas. Flexibilidad En Tu Vuelo. Check-in 100 % Online.
                </Card.Text>
            </Card.Body>
            <Card.Body>
                <Card.Link href="https://es.wikipedia.org/wiki/Avianca_(Colombia)">Wikipedia</Card.Link>
            </Card.Body>
        </Card>
    );
};

const Avianca = (props): React$Element<React$FragmentType> => {
    return (
        <React.Fragment>
            <PageTitle breadCrumbItems={[{ label: 'Latan', path: '/pages/latan', active: true }]} title={''} />
            <Row>
                <Col lg={6}>
                    <MensajeBienvenida mensage={props.mensage} />
                </Col>
                <Col lg={6}>
                    {' '}
                    <CardWithImage2 />
                </Col>
            </Row>
        </React.Fragment>
    );
};

export default Avianca;
